.tooltip,
.trim {
  position: relative;
  display: inline-flex;
  overflow: visible;
  z-index: 1000;
}
.nowrap {
  white-space: nowrap;
}
.tooltip {
  position: relative;
}
.tooltip .tip {
  will-change: transform opacity;
  position: absolute;
  filter: drop-shadow($tip-sh);
  width: 100%;
  color: $dark;
  display: flex;
  justify-content: flex-start;
  animation: 0.125s ease-in tooltip-anim;
}
.tooltip .tip .value {
  border-radius: 3px;
  padding: 0.125em 0.25em;
  background-color: #fff;
  word-break: break-all;
  display: flex;
  justify-content: center;
  align-items: center;
}
.tooltip .tip .tip-txt {
  padding: 0.25em;
  overflow: visible;
  display: inline;
  margin: 0;
  font-size: 0.9em;
  font-weight: normal;
}
.tooltip .tip:after,
.tooltip .tip:before {
  border: solid transparent;
  content: ' ';
  height: 0;
  width: 0;
  position: absolute;
  z-index: 100;
}
.tooltip .tip:after {
  border-width: 5px;
}
.tooltip .tip:before {
  border-width: 6px;
}
.tooltip .tip.top:after,
.tooltip .tip.top:before {
  top: 100%;
  left: 50%;
}
