}
.tooltip .copy-txt {
  display: inline;
}
.tooltip .copy-txt textarea {
  border: none;
  margin: 0;
  padding: 0;
  background-color: inherit;
  opacity: 0;
  width: 1px;
  heigth: 1px;
}
.tooltip button.close {
  line-height: 1em;
  height: 1em;
}
.tooltip button.close:after {
  top: 0.25em !important;
