.event-call {
  margin: 0;
}
.event-call .index {
  font-size: 0.8em;
}
.event-call li.event-name {
  font-weight: bold;
  color: #1faec2;
}
.event-call ul {
  font-size: 0.7em;
  font-family: monospace;
  font-size: 0.9em;
  list-style: none;
  display: flex;
  flex-flow: row;
  align-items: flex-end;
  font-style: italic;
  font-weight: normal;
  margin: 0;
  padding: 0;
}
.event-call ul li {
  color: #568475;
}
.event-call ul,
.event-call li {
  margin: 0 0.25em 0 0;
  display: flex;
}
.event-call ul:last-child:after,
.event-call li:last-child:after {
  font-weight: bold;
}
.event-call .args::before {
  content: '(';
}
.event-call .args::after {
  content: ')';
}
.event-call .args .type {
  color: #1faec2;
}
.event-call .args .type:after {
  content: '';
}
.event-call .args .name {
  font-size: 0.9em;
  color: #9fdfd1;
}
.event-call .args .name:after {
  content: ',';
  font-size: 1em;
}
.event-call .args .name:last-child:after {
  content: none;
}
