.block-box {
  display: flex;
  flex: 1 1 100%;
  will-change: opacity;
}
.block-box .title {
  padding: 0;
  margin: 0;
}
.blockbox-enter-active {
  transition: opacity 0.5s;
  opacity: 1;
}
.blockbox-enter,
.block-box-leave-to {
  opacity: 0;
}
