.data-table {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}
.table-ctrls {
  display: flex;
  justify-content: flex-end;
  margin: 0 1em;
}
.unsortable > .field-title {
  color: #568475;
}
.unsortable > .field-title .icon svg {
  fill: #568475 !important;
}
.sort {
  display: flex;
  justify-content: center;
  align-items: center;
}
.sort .field-title {
  display: flex;
  justify-content: center;
  align-items: center;
}
.sort button {
  display: flex;
}
.sort div {
  display: flex;
}
.sort .icon {
  margin: 0 0.5em 0 0;
}
.sort .sort-icon {
  margin: 0 0.25em;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #1aad8d;
  width: 1em;
  height: 1em;
  border-radius: 50%;
}
.sort .sort-icon svg.svg-icon * {
  fill: #102026;
  display: flex;
}
sub {
  color: #fff;
}
.has-sort {
  padding: 0 !important;
}
