.switch {
  display: flex;
  align-items: center;
}
.switch label {
  display: inline-block;
  position: relative;
  width: 2em;
  height: 1em;
  margin: 0;
  padding: 0;
}
.switch .label {
  margin-left: 0.5em;
  font-size: 0.9em;
}
.switch input[type="checkbox"] {
  display: none;
  border: none;
}
.switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #568475;
  transition: 0.4s;
  display: inline;
  box-shadow: inset 0px 1px 1px rgba(0,0,0,0.5);
}
.switch .slider:before {
  position: absolute;
  content: '';
  height: 0.6em;
  width: 0.6em;
  left: 0.2em;
  bottom: 0.2em;
  background-color: #fff;
  box-shadow: 1px 1px 1px rgba(0,0,0,0.3);
  transition: 0.4s;
}
.switch input:checked + .slider {
  background-color: #1aad8d;
}
.switch input:focus + .slider {
  box-shadow: 0 0 1px #1aad8d;
}
.switch input:checked + .slider:before {
  transform: translateX(1em);
}
.switch .slider.round {
  border-radius: 1em;
}
.switch .slider.round:before {
  border-radius: 50%;
}
