.data-item {
  min-width: 100%;
  width: 100%;
}
.data-item .items {
  min-width: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 0.5em 0em;
}
.data-item .item {
  display: flex;
  flex: 1 1 100%;
  align-items: center;
  padding: 0.5em 0em;
  overflow: visible;
}
.data-item .item {
  font-size: 1em;
}
.data-item .item .field-icon,
.data-item .item .field-title {
  margin-right: 0.5em;
}
.data-item .item .field-title {
  flex: 1;
  margin: 0 1em 0 2em;
  justify-content: flex-start;
}
.data-item .item .data-field {
  margin: 0 2em 0 0em !important;
  flex: 5;
}
.field-value {
  display: inline-flex;
}
.custom {
  display: flex;
  flex-flow: column wrap;
  margin: 0 2em 0 0em !important;
  flex: 5;
}
.custom-item {
  display: flex;
  flex: 1 1 100%;
  align-items: center;
  overflow: visible;
  padding: 0.5em 0em;
}
.custom-item .field-title {
  flex: 1;
  margin: 0 1em 0 2em;
  justify-content: flex-start;
}
.field-icon,
.field-title {
  color: #1aad8d;
}
.field-title {
  text-transform: capitalize;
  font-weight: bold;
}
.item-title {
  text-transform: capitalize;
  display: inline-flex;
}
.item-title .field-value {
  display: inline-flex;
}
.item-title div::before,
.item-title span::before {
  content: '\00a0';
}
.field-title.big-field {
  justify-content: flex-start;
  margin-bottom: 0.5em;
}
.field-title.big-field,
.big-field.custom {
  margin: 0 1em 0em 2em;
}
.field-title.data-table {
  margin: 0 !important;
  justify-content: center !important;
  margin-top: 1em !important;
}
@media screen and (min-width: 900px) {
  .data-item .big-field.custom {
    min-width: 20em;
  }
}
/*# sourceMappingURL=src/components/DataItem.css.map */