.collapsible-container {
  display: flex;
  flex: 1 1;
  max-width: 100%;
  flex-flow: column wrap;
  border: solid #14272f 1px;
}
.collapsible-container .header {
  display: flex;
  flex-flow: row nowrap;
  margin: 0 1em;
  color: #1aad8d;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: space-between;
}
.collapsible-container .header .svg-icon,
.collapsible-container .header svg {
  fill: #1aad8d !important;
}
.collapsible-container .header :hover {
  transition: all 0.5s ease;
  color: #e59b4d;
}
.collapsible-container .header :hover .svg-icon,
.collapsible-container .header :hover svg {
  fill: #e59b4d !important;
}
.collapsible-container .header .title {
  margin: 0.25em;
}
.collapsible-container .header .title div {
  margin: 0 1em 0 0;
}
.collapsible-container .content {
  display: flex;
  flex: 1 1 100%;
}
.collapsed {
  box-shadow: 1px 1px 2px rgba(0,0,0,0.7);
  flex: 0;
}
.expanded {
  display: flex;
  border: rgba(26,173,141,0.2) solid 1px;
  background: none !important;
}
.expand-enter-active,
.expand-leave-active {
  transition: all 0.25s ease-in;
  opacity: 1;
}
.expand-enter,
.expand-leave-to {
  opacity: 0;
}
