.margin-less {
  margin-bottom: 0;
}
.search-msg {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column wrap;
  transition: all 0.5s ease;
  flex: 0 1 100%;
  opacity: 1;
  position: relative;
  margin-bottom: -2em;
}
.search {
  flex-flow: row wrap;
}
.search button {
  margin: 0 0.5rem 0 0;
}
.search input {
  text-align: center;
}
.msg-trans {
  will-change: opacity;
}
.msgtrans-enter-active {
  opacity: 0;
}
.msgtrans-leave-to {
  transition: all 0.5s ease;
  transform: translateY(-1em);
  opacity: 0;
}
