.spinner {
  min-height: 100vh;
  min-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.spinner .cubes .fill {
  fill: #1aad8d;
  stroke: #568475;
}
.spinner .cubes svg {
  stroke: #414042;
  overflow: visible;
}
