input.page {
  width: 3em;
}
.pages {
  width: 100%;
  display: flex;
  margin: 1em;
  justify-content: space-evenly;
}
ul.page-numbers {
  display: flex;
  flex-flow: row nowrap;
  color: #1aad8d;
  list-style: none;
}
ul.page-numbers li {
  margin: 0 0.25em;
  min-width: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
}
ul.page-numbers li.selected {
  padding: 0.0625em 0.125em;
  border-radius: 0.25em;
  border-color: rgba(26,173,141,0.2) dashed 1px;
}
.page-button .svg-icon * {
  fill: #127862;
}
