



































































































































































































































































.page-header
  width 100%

.data-page h2.title
  text-transform capitalize
  // align-self flex-start

.page, .section
  will-change opacity
  animation-name page-anim
  animation-duration 0.5s
  animation-timing-function ease-in
  opacity 1

  @keyframes page-anim
    0%
      opacity 0

    100%
      opacity 1

.page-header
  margin-bottom 2em

.messages
  font-size 0.9em
  text-align center
