ul {
  list-style-type: square;
  padding: 0 0 0 1em;
  margin: 0;
}
ul.plain {
  list-style: none;
  padding: 0;
  margin: 0;
}
ul.flex {
  display: flex;
  flex-flow: row wrap;
}
ul.flex li {
  flex: 0 1 100%;
}
ul.flex li.half {
  display: flex;
  align-items: center;
  flex: 1 1 50%;
}
ul.flex li.half .data-field {
  flex: 1;
  width: 100% !important;
}
@media screen and (max-width: 480px) {
  ul.flex li.half {
    flex: 1 1 100%;
  }
}
dl {
  display: inline-block;
  clear: both;
}
dt,
dd {
  float: left;
}
dt {
  font-weight: bold;
  clear: both;
  margin-bottom: 1em;
}
dl.data dt {
  font-weight: bold;
}
ul.data {
  list-style: none;
  display: flex;
  flex-flow: column wrap;
  align-items: flex-start;
  max-width: 100%;
}
ul.data li {
  flex: 1;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
ul.data li .field-title {
  font-weight: bold;
  text-transform: capitalize;
}
ul.data li .field-title:after {
  content: ':';
}
ul.data li .field-title {
  flex: 1;
}
ul.data li .field-value {
  flex: 1;
  display: inline-flex;
}
ul.prev-next {
  width: 100%;
  list-style: none;
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  max-width: 100%;
}
ul.prev-next li {
  flex: 0 1 auto;
  margin: 0;
}
ul.prev-next .total {
  flex: 2 0 auto;
  text-align: center;
}
ul.prev-next .prev {
  margin-left: 1em;
}
ul.prev-next .prev .icon,
ul.prev-next .prev .svg-icon {
  margin-right: 1em;
}
ul.prev-next .next {
  margin-right: 1em;
}
ul.prev-next .next .icon,
ul.prev-next .next .svg-icon {
  margin-left: 1em;
}
ul.list,
.list-head,
.list-item,
.list-buttons {
  list-style: none;
  padding: 0;
  margin: 0;
}
ul.list li,
.list-head li,
.list-item li,
.list-buttons li {
  padding: 0;
  margin: 0;
}
ul.list {
  display: flex 1 100%;
  flex-flow: column nowrap;
  min-width: 100%;
}
ul.list li {
  flex: 2 1 100%;
  padding: 0.5rem 1rem;
}
ul.list li.even {
  background: #172d36;
  color: #1aad8d;
  border: #14272f solid 1px;
}
ul.list li.odd {
  background: #14272f;
  color: #1aad8d;
  border: #172d36 solid 1px;
}
ul.list-head,
ul.list-buttons {
  display: inline-flex;
}
ul.list-head li,
ul.list-buttons li {
  padding: 0;
}
ul.list-head li:first-child {
  letter-spacing: 0.0625em;
  font-weight: bold;
}
ul.list-buttons {
  display: flex;
}
ul.list-buttons li {
  text-align: right;
  justify-content: center;
}
ul.list-item {
  display: flex;
  flex-flow: row wrap;
  padding: 0 0.5em;
}
ul.list-item li {
  padding: 0;
  flex: 1 0 100%;
  margin-bottom: 0.5em;
}
ul.list-item li.col {
  flex: 1 2;
}
ul.list li.min {
  display: flex;
  justify-content: flex-end;
  border: none;
}
ul.list li.min ul.list-head {
  flex: 2 2 60%;
}
ul.list li.min li {
  display: inline-flex;
  display: none;
}
ul.list li.min ul.list-head,
ul.list li.min ul.list-buttons {
  display: inline-flex;
  flex-flow: row nowrap;
  align-items: flex-start;
}
ul.list li.min ul.list-head li,
ul.list li.min ul.list-buttons li {
  display: inline;
}
ul.list li.min ul.list-head li:first-child {
  font-weight: normal;
}
ul.list li.min li.action {
  display: inline-flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  align-items: flex-end;
}
ul.list li.min li.action button {
  font-size: 0.6em;
  margin-left: 0.5em;
}
.list.dark {
  color: #1aad8d;
}
.raw-list ul {
  list-style-type: none;
}
.data-field ul {
  margin: 0;
  padding: 0;
}
.data-field,
.data-field > a,
.data-field > .tooltip,
max-width 100% {
  display: flex;
  position: relative;
  word-wrap: break-word;
  overflow-wrap: break-word;
  word-break: break-word;
  justify-content: center;
  align-items: center;
}
.field-value {
  overflow-wrap: break-word;
  word-wrap: break-word;
  -ms-word-break: break-all;
  word-break: break-all;
  word-break: break-word;
  -ms-hyphens: auto;
  -moz-hyphens: auto;
  -webkit-hyphens: auto;
  hyphens: auto;
}
.flex-table td .data-field {
  width: 100%;
}
.data-field.items-list ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}
.data-field.items-list ul li {
  margin: 0 0 0 1em;
}
.data-field.items-list ul :first-child {
  margin: 0;
}
