.pending-blocks {
  justify-content: center;
  align-items: center;
  border: solid #14272f 1px;
  background: #112329;
  border-radius: $box-border-radius;
  padding-bottom: 0.5rem;
}
.pending-blocks h4 {
  padding: 0;
  margin: 0;
}
.pending-blocks .cubes .fill {
  fill: #e59b4d;
}
.pending-blocks .cubes svg {
  stroke: #414042;
  overflow: visible;
}
.pending-blocks .chart-title {
  margin-bottom: 1em;
}
.miners {
  min-width: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
}
.miner {
  flex: 1;
  position: relative;
  display: flex;
  align-items: center;
  flex-flow: column nowrap;
}
.miner svg {
  overflow: visible;
}
.miner svg .cube {
  fill: #1aad8d;
  stroke: #127862;
}
.miner .name {
  font-size: 0.8em;
}
.miner .cubes {
  display: inline-block;
}
.miner .blocks {
  position: absolute;
  right: 1.5em;
  text-align: center;
}
.miner .blocks span {
  width: 1.6em;
  height: 1.6em;
  min-width: 1.6em;
  min-height: 1.6em;
  border-radius: 50%;
  padding: 0.133333333333333em;
  line-height: 1.6em;
  font-size: 0.8em;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #1aad8d;
  color: #fff;
  fill: #fff;
  border: #1aad8d solid 1px;
  background: none;
}
