body,
.wrapper {
  margin: 0;
  border: 0;
  display: flex;
  flex-direction: column;
  max-width: 100%;
}
.wrapper,
.main {
  min-height: 100vh;
  max-width: 100%;
}
.page,
.data-page,
.centered,
.section {
  max-width: 100%;
}
.data-page,
.page,
.section {
  width: 100%;
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  justify-content: flex-start;
}
.main,
.cols,
.home {
  display: flex;
  flex: 1 1 100%;
  flex-flow: row wrap;
}
.main {
  margin: 0 1rem;
}
.hero {
  display: flex;
  flex: 1 1 100%;
  flex-flow: row wrap;
}
.hero .col-a .box,
.hero .col-b .box {
  margin: 0.5em;
}
.col-a,
.col-b {
  flex: 0 1 100%;
  max-width: 100%;
}
.col-a {
  order: 1;
}
.col-b {
  order: 2;
}
@media screen and (min-width: 600px) {
  .main {
    margin: 0 2rem;
  }
}
@media screen and (min-width: 900px) {
  .main {
    margin: 0 5rem;
  }
  .col-a,
  .col-b {
    margin: 0;
    flex: 1 1 50%;
    max-width: 50%;
  }
  .hero {
    flex-flow: row nowrap;
  }
  .hero .col-a,
  .hero .col-b {
    flex: 0 1 50%;
    align-items: flex-start;
  }
  .hero .col-a .box,
  .hero .col-b .box {
    margin: 0 0.5em;
  }
}
@media screen and (min-width: 1200px) {
  .main {
    margin: 0 10rem;
  }
  .col-a {
    margin: 0;
  }
  .col-b {
    margin: 0;
  }
}
@media screen and (min-width: 1600px) {
  .main {
    margin: 0 15rem;
  }
}
