button {
  cursor: pointer;
  color: #1aad8d;
  background-color: transparent;
  border: none;
}
button .arrow {
  border-color: #1aad8d;
}
button .svg-icon {
  fill: #1aad8d;
  width: 1em;
  height: 1em;
}
button hover {
  color: #e59b4d;
}
button hover .arrow {
  border-color: #e59b4d;
}
button .svg-icon {
  fill: #e59b4d;
}
button.icon {
  width: 1.5em;
  height: 1.5em;
  padding: 0.25em;
}
button.icon .svg-icon,
button.icon .icon {
  width: 1em;
  height: 1em;
}
.btn {
  padding: 0.25em 0.5em;
  border-radius: 0.1em;
  box-shadow: 1px 1px 1px rgba(0,0,0,0.5);
  margin: 0 0.25em;
  font-weight: bold;
}
button.big {
  font-size: 1em;
  width: 2.5em;
  height: 2.5em;
}
button.big .svg-icon {
  width: 1.75em;
  height: 1.75em;
}
button.big .badge {
  font-size: 0.8em !important;
}
button.big-txt {
  font-size: 1.25em;
}
button.menu {
  width: 2.5em;
  height: 2.5em;
  padding: 0;
}
button.menu .svg-icon {
  width: 2em;
  height: 2em;
}
button.circle {
  border-radius: 50%;
  border: #1aad8d solid 1px;
  padding: 0.4em;
}
button.circle .svg-icon {
  width: 1.5em;
  height: 1.5em;
}
button.circle hover {
  border-color: #e59b4d;
}
.btn.badge,
.w-badge {
  position: relative;
  margin: 0.75em 0.5em 0.75em 0.25em;
  display: inline-block;
}
.btn.badge .badge,
.w-badge .badge {
  width: 1.5em;
  height: 1.5em;
  min-width: 1.5em;
  min-height: 1.5em;
  border-radius: 50%;
  padding: 0.125em;
  line-height: 1.5em;
  font-size: 0.75em;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #1aad8d;
  color: #fff;
  fill: #fff;
  position: absolute;
  bottom: -0.75em;
  right: -0.75em;
}
.btn.badge .badge .arrow,
