}
.chart .header {
  display: flex;
}
.chart .header .icon,
.chart .header .svg-icon {
  width: 1em;
  height: 1em;
  margin-right: 1em;
  top: 0.5em;
}
.chart svg,
.node-chart svg {
  overflow: visible;
  fill: #1aad8d;
}
.chart-title {
  font-weight: normal;
  margin-left: 2em;
  font-size: 1em;
}
.chart-title button.max {
  position: absolute;
  top: 1em;
  right: 2em;
  z-index: 50;
  pointer-events: initial;
}
.chart-title button.max:hover {
  color: #e59b4d;
}
.chart-title button.max .dialog {
  position: absolute;
  z-index: 999;
  left: 0;
  top: 0;
  min-width: 100%;
}
.max-chart {
  padding: 1em 1.5em;
}
.dark-chart {
  background-color: #112329;
  border: solid #14272f 1px;
}
.dark-chart .line {
  stroke: #1aad8d;
}
.dark-chart .label {
  fill: #1aad8d;
}
.dark-chart .axis {
  stroke: #1aad8d;
}
.dark-chart .axis-label {
  fill: #37e1bc;
}
.dialog-header .title {
  color: #1aad8d;
}
.curve-back path {
  opacity: 0.2;
  stroke: none;
}
.page-title {
  font-size: 2.5em;
}
ul.inline {
  display: inline;
  margin: 0;
  padding: 0;
  color: #1aad8d;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.5);
}
.inline {
  list-style: none;
}
.inline li {
  display: inline-block;
}
.inline li:after {
  content: ' ';
  margin: 0 0.5em;
}
.cross-cursor {
  cursor: crosshair;
}
.suffix {
  font-size: 0.8em;
}
.med {
  font-size: 2em;
}
.row {
  display: flex;
  flex-flow: row nowrap;
}
.col {
  display: flex;
  justify-content: center;
  align-items: center;
}
.full-w {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1 100%;
}
.space-v {
  margin: 0.5em;
}
.nav-bar {
  display: flex;
  flex-flow: row nowrap;
}
.nav-bar .col {
  flex: 1 1 100%;
}
.nav-bar .col:first-of-type * {
  text-align: left;
}
.nav-bar .col:last-of-type {
  text-align: right;
}
.big-number {
  font-size: 0.6em;
}
.soft {
  color: #568475;
  font-size: 0.9em;
}
.link {
  color: #1aad8d;
  cursor: pointer;
}
.link .svg-icon,
.link svg {
  fill: #1aad8d !important;
}
.link :hover {
  transition: all 0.5s ease;
  color: #e59b4d;
}
.link :hover .svg-icon,
.link :hover svg {
  fill: #e59b4d !important;
}
.pill {
  display: flex;
  justify-content: center;
  align-items: center;
  width: auto;
  height: 1.5em;
  line-height: 1em;
  background: #1aad8d;
  list-style: none;
  border-radius: 0.25em;
  margin: 0 0 0 0.5em;
  color: #102026;
}
.pill button svg * {
  fill: #64e8cb;
}
.pill button:hover svg * {
  fill: #102026;
}
.pill :first-child {
  margin: 0 0.25em 0 0.5em;
}
.pill :last-child {
  margin: 0 0.25em 0 0.5em;
}
.pill .svg-icon {
  margin: 0;
}
.pill button,
.pill button.close {
  width: 1em;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.9em;
  height: 1em;
  border-radius: 1em;
  background: #1aad8d;
}
.pill small {
  color: #fff;
}
.text {
  margin: 0 1rem;
}
.frame {
  border: $border-color dashed 1px;
  padding: 0.5em 1em;
  margin: 0 0 0.5rem 0;
}
.raw {
  font-size: 0.7em;
  font-family: monospace;
}
.raw ul {
  list-style: none;
}
.bold {
  font-weight: bold;
}
.hidden {
  display: none;
}
svg.svg-icon {
  fill: #1aad8d;
  width: 8em;
  height: 8em;
  overflow: visible;
}
.svg-icon .cube .bg {
  fill: #fff;
}
.svg-icon .cube .fill {
  fill: #1aad8d;
  opacity: 0.9;
}
.svg-icon .cube .top {
  fill: rgba(255,255,255,0.2);
}
.svg-icon .cube .front {
  fill: rgba(0,0,0,0.3);
}
.svg-icon .cube .side {
  fill: rgba(0,0,0,0.15);
}
svg.small {
  width: 0.6em !important;
  height: 0.6em !important;
}
.small .svg-icon {
  width: 1.125em !important;
  height: 1.125em !important;
}
svg.medium {
  width: 1.5em !important;
  height: 1.5em !important;
}
.tooltip button.copy {
  color: #1aad8d;
  cursor: pointer;
}
.tooltip button.copy .svg-icon,
.tooltip button.copy svg {
  fill: #1aad8d !important;
}
.tooltip button.copy :hover {
  transition: all 0.5s ease;
  color: #e59b4d;
}
.tooltip button.copy :hover .svg-icon,
.tooltip button.copy :hover svg {
  fill: #e59b4d !important;
}
.tooltip .tip {
  filter: drop-shadow(1px 1px 1px rgba(0,0,0,0.3));
  color: #127862;
}
.tooltip .tip .value {
  align-items: center;
}
.tooltip .points .icon {
  color: #1aad8d;
}
.copying {
  color: #1aad8d;
}
.tabs {
  justify-content: flex-start;
  min-width: 100%;
}
.tabs-titles {
  display: flex;
  flex: 1 0 100%;
  min-width: 100%;
  flex-flow: row nowrap;
  justify-content: flex-start;
}
.tab-title {
  display: flex;
  margin: 0 0.5em 0 0;
  justify-content: center;
  align-items: center;
}
.tab-title .title {
  margin: 0 1em;
  text-transform: capitalize;
}
.tab-title .loading-circle .circle {
  fill: none;
  stroke: #1aad8d;
}
.tab-title.active {
  background: #1aad8d;
  font-weight: bold;
  color: #fff;
}
.tab-title.active .loading-circle .circle {
  stroke: #fff;
}
.tab-content {
  min-width: 100%;
  display: block;
  flex: 1;
}
.block ul,
.transaction ul {
  list-style: none;
}
td.from,
th.from {
  text-align: right;
}
td.to,
th.to {
  text-align: left;
}
.blocks ul,
.block-data ul {
  margin: 0;
  padding: 0;
}
.blocks ul li,
.block-data ul li {
  list-style: none;
}
.block {
  flex: 1 1 100%;
  display: flex;
  flex-flow: row;
  border-left: solid 3px;
}
.block .block-icon {
  flex: 1;
}
.block .block-icon .svg-icon {
  fill: #497d3b;
  width: 2em;
  height: 2em;
}
.block .block-number {
  color: #497d3b;
  margin-left: 0.25rem;
  font-size: 1.25em;
  font-weight: 400;
}
.centered {
  margin: 0 auto;
}
.home {
  display: flex;
}
.last-blocks {
  flex-flow: row wrap;
}
.auto-update {
  display: flex;
  justify-content: center;
  width: 100%;
}
.hero {
  margin-top: 2rem;
  max-width: 100%;
}
.last-block {
  display: flex;
  flex: 7;
  width: 100%;
}
.last-block ul.block-data {
  margin: 0;
  padding: 0;
  flex-flow: column nowrap;
}
.last-block ul.block-data li {
  width: 100% !important;
}
.last-block .block-number * {
  font-size: 2em;
}
.pending-blocks {
  flex: 3;
}
.field-title + .data-field {
  margin: 0 0 0 0.5em;
}
.flex-table .data-field {
  margin: 0;
}
.odd {
  background: #14272f;
}
.even {
  background: #172d36;
}
.data-field {
  justify-content: flex-start;
}
td .data-field {
  justify-content: center;
}
.w-trans {
  transition: opacity 1s ease;
  opacity: 1;
}
.head-trans-enter-active {
  opacity: 0;
}
.top-page {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.9em;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.5);
  background: #14272f;
  border-bottom: 1px solid #172d36;
}
