.chart-container {
  display: flex;
  justify-content: space-between;
  max-width: 100%;
}
.tx-chart {
  max-height: 100%;
  height: auto;
}
.tx-chart .dummy-bar:hover {
  cursor: pointer;
}
.tx-chart svg {
  overflow: visible;
}
.tx-chart .curve path {
  stroke: #497d3b;
  stroke-width: 1;
}
